/* Energy Price Chart Styles */

.chart-wrapper {
  font-family: 'Fira Sans', sans-serif;
  background-color: #192027;
  color: #FFFFFF;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 20px;
}

.chart-container {
  position: relative;
  height: 400px;
  width: 100%;
}

/* Stats styling */
#priceStats {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  gap: 15px;
}

.stat-box {
  flex: 1;
  padding: 15px;
  border-radius: 8px;
  text-align: center;
  background-color: #1E2731;
  border-left: 3px solid #506678;
  transition: transform 0.2s;
}

.highest-price {
  border-left-color: #506678;
}

.lowest-price {
  border-left-color: #506678;
}

.average-price {
  border-left-color: #506678;
}

.stat-label {
  font-size: 14px;
  color: #a0a0a0;
  margin-bottom: 5px;
}

.stat-value {
  font-size: 22px;
  font-weight: 700;
  color: #FFFFFF;
}

.stat-indicator {
  font-size: 12px;
  color: #a0a0a0;
  margin-top: 5px;
}

/* Responsive styles */
@media (max-width: 768px) {
  #priceStats {
    flex-direction: column;
  }
  
  .stat-box {
    margin-bottom: 10px;
  }
  
  .chart-container {
    height: 300px;
  }
}

@media (max-width: 480px) {
  .chart-container {
    height: 250px;
  }
  
  .stat-value {
    font-size: 18px;
  }
  
  .stat-label {
    font-size: 12px;
  }
} 